import { _decorator, Component, instantiate, Prefab, Vec3 } from "cc";
const { ccclass, property } = _decorator;

@ccclass('GameArea')
export class GameArea extends Component {

    @property({
        type: Prefab,
    })
    BlockPrefab: Prefab;

    public createBlock() {
        for (let i = 0; i < 64; i++) {
            let node = instantiate(this.BlockPrefab);
            let x = (i % 8) * 80;
            let y = Math.floor(i / 8) * 80;
            node.setPosition(new Vec3(x, y, 0));
            this.node.getChildByName("Container").addChild(node);
        }
    }
}


